import React, { useState } from "react";
import {
  Table,
  Typography,
  Button,
  Form,
  Input,
  Menu,
  Dropdown,
  DatePicker,
} from "antd";
import clsx from "clsx";
import classes from "./styles.module.scss";
import {
  PushpinOutlined,
  CalendarOutlined,
  CloseOutlined,
  ArrowUpOutlined,
  ArrowDownOutlined,
} from "@ant-design/icons";

const ProductsTable = (props) => {
  const { type, newdata } = props;
  const [state, setState] = useState({
    selectedRowKeys: [],
  });
  const [dropdown, setDropdown] = useState(false);
  const [dateRangeLenght, setDateRangeLenght] = useState(null);

  const { selectedRowKeys } = state;
  const { Text } = Typography;
  const { RangePicker } = DatePicker;

  const data = [];

  newdata?.products.forEach((product, index) => {
    data.push({
      index,
      key: product.id,
      name: product.name,
      status: product.inStock,
      media: product.media[0].url,
    });
  });

  const rangePickerHandler = (date) => {
    setDateRangeLenght(date);
  };

  const closeDatepicker = () => {
    setDropdown(false);
  };

  const saveDates = () => {
    setDropdown(false);
  };

  const menu = (
    <Menu>
      <Menu.Item className={classes.menuItem} key={1}>
        <RangePicker
          open={dropdown}
          style={{ margin: "7px 0 334px 0", width: "100%" }}
          dropdownClassName={classes.rangePicker}
          onChange={rangePickerHandler}
          value={dateRangeLenght}
          renderExtraFooter={() => (
            <div className={classes.datepickerFooter}>
              <Button
                className={classes.datepickerFooterButton}
                onClick={closeDatepicker}
              >
                Cancel
              </Button>
              <Button
                className={classes.datepickerFooterButton}
                onClick={saveDates}
              >
                Save
              </Button>
            </div>
          )}
        />
      </Menu.Item>
    </Menu>
  );

  const columns = [
    {
      title: () => (
        <div className={classes.theadCell}>
          <div className={classes.theadCellRow}>
            <div className={classes.selected}>
              {state.selectedRowKeys.length} selected
            </div>
            {type !== "previouslyExcluded" && (
              <Button
                type="default"
                icon={<PushpinOutlined className={classes.theadButtonIcon} />}
                className={classes.theadButton}
              />
            )}

            <Dropdown
              overlay={menu}
              placement="bottomLeft"
              trigger={["click"]}
              visible={dropdown}
              onVisibleChange={setDropdown}
            >
              <Button
                type={
                  dropdown || dateRangeLenght !== null ? "primary" : "default"
                }
                icon={<CalendarOutlined className={classes.theadButtonIcon} />}
                className={classes.theadButton}
              />
            </Dropdown>

            <Button
              disabled={type === "previouslyExcluded" && false}
              icon={<CloseOutlined className={classes.theadButtonIcon} />}
              className={clsx(classes.theadButton, classes.theadButtonDanger)}
            />
          </div>

          {type !== "previouslyExcluded" && (
            <Form layout={"horizontal"} className={classes.form}>
              <Form.Item
                label={"Narrow down"}
                tooltip={{
                  title: "Example",
                }}
                style={{ margin: "0" }}
              >
                <Input placeholder={"Input placeholder"} />
              </Form.Item>
            </Form>
          )}

          <div className={classes.columnsTitleBox}>
            <div className={classes.planStatusColumnsTitle}>Plan status</div>
            <div className={classes.arrangementColumnsTitle}>
              {type !== "previouslyExcluded" ? "Arrangement" : ""}
            </div>
          </div>
        </div>
      ),
      dataIndex: "name",
      key: "name",
      render: (text, row, index) => (
        <div className={classes.product}>
          <div className={classes.col}>
            <div className={classes.imgBox}>
              <img src={row.media} alt="" className={classes.img} />
            </div>
            <Text className={classes.name}>{row.name.en}</Text>
          </div>
          <div className={classes.col}>
            {data.length > 1 ? (
              (index === 2 || index === 3) && (
                <div className={classes.dateBox}>
                  <CalendarOutlined className={classes.calendarIcon} />
                  Dec 6 - Dec 19
                </div>
              )
            ) : (
              <div className={classes.dateBox}>
                <CalendarOutlined className={classes.calendarIcon} />
                Dec 6 - Dec 19
              </div>
            )}

            {index < 4 && (
              <div className={classes.buttonBox}>
                {data.length > 1 && (
                  <>
                    <Button
                      disabled={index === 0 && true}
                      className={classes.posButton}
                      icon={
                        <ArrowUpOutlined className={classes.posButtonIcon} />
                      }
                    />
                    <Button
                      disabled={index === 3 && true}
                      className={classes.posButton}
                      icon={
                        <ArrowDownOutlined className={classes.posButtonIcon} />
                      }
                    />
                  </>
                )}
              </div>
            )}
          </div>
        </div>
      ),
    },
  ];

  const onSelectChange = (selectedRowKeys) => {
    setState({ selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  return (
    <Table
      bordered={true}
      rowSelection={rowSelection}
      columns={columns}
      dataSource={data}
      pagination={{
        position: ["bottomLeft"],
        defaultPageSize: 10,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "30"],
        hideOnSinglePage: true,
      }}
    />
  );
};
export default ProductsTable;
