import React from "react";
import { Menu } from "antd";
import {
  AppstoreOutlined,
  MailOutlined,
  SettingOutlined,
} from "@ant-design/icons";

const Navbar = () => {
  const { SubMenu } = Menu;
  const [openKeys, setOpenKeys] = React.useState(["sub1"]);
  const rootSubmenuKeys = ["sub1", "sub2", "sub4"];

  const onOpenChange = (keys) => {
    const latestOpenKey = keys.find((key) => openKeys.indexOf(key) === -1);
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };

  return (
    <Menu
      defaultSelectedKeys={["1"]}
      defaultOpenKeys={["sub1"]}
      mode="inline"
      openKeys={openKeys}
      onOpenChange={onOpenChange}
    >
      <SubMenu key="sub1" icon={<MailOutlined />} title="Navigation One">
        <Menu.ItemGroup key="g1" title="Navigation Group">
          <Menu.Item key="1">Navigation Item</Menu.Item>
          <Menu.Item key="2">Navigation Item</Menu.Item>
        </Menu.ItemGroup>
        <Menu.ItemGroup key="g2" title="Navigation Group">
          <Menu.Item key="3">Navigation Item</Menu.Item>
          <Menu.Item key="4">Navigation Item</Menu.Item>
        </Menu.ItemGroup>
      </SubMenu>
      <SubMenu key="sub2" icon={<AppstoreOutlined />} title="Navigation Two">
        <Menu.Item key="5">Navigation Item</Menu.Item>
        <Menu.Item key="6">Navigation Item</Menu.Item>
        <SubMenu key="sub3" title="Submenu">
          <Menu.Item key="7">Navigation Item</Menu.Item>
          <Menu.Item key="8">Navigation Item</Menu.Item>
        </SubMenu>
      </SubMenu>
      <SubMenu key="sub4" icon={<SettingOutlined />} title="Navigation Three">
        <Menu.Item key="9">Navigation Item</Menu.Item>
        <Menu.Item key="10">Navigation Item</Menu.Item>
        <Menu.Item key="11">Navigation Item</Menu.Item>
        <Menu.Item key="12">Navigation Item</Menu.Item>
      </SubMenu>
    </Menu>
  );
};
export default Navbar;
