import React, { useState } from "react";
import { Row, Col, Tabs, Typography } from "antd";
import SearchBox from "../SearchBox";
import ProductsTable from "../ProductsTable";
import newdata from "../../data/products-visely.json";
import previouslyExcludedData from "../../data/previously-excluded.json";

const SearchPage = () => {
  const [openDatePicker, setOpenDatePicker] = useState(false);
  const { TabPane } = Tabs;
  const { Title } = Typography;

  const handleDatePicker = () => {
    setOpenDatePicker((prevState) => !prevState);
  };

  return (
    <Row>
      <Col span={24}>
        <SearchBox
          hintLabel="SEARCH"
          hintTitle="— Pin or exclude products for specific search terms or SKU. Overwrite the search algorithm."
          label="Search term"
          tooltipTitle="Example"
          placeholder="Dress"
          marginBottom={6}
        />
        <Tabs defaultActiveKey="0" tabPosition={"top"}>
          {newdata.searchCategories.map((item, i) => (
            <TabPane tab={`${item.name}`} key={i} disabled={item.disabled}>
              <ProductsTable
                newdata={newdata}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
              <Title level={5} style={{ margin: "21px 0 0" }}>
                Previously Excluded
              </Title>

              <ProductsTable
                type={"previouslyExcluded"}
                newdata={previouslyExcludedData}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
            </TabPane>
          ))}
        </Tabs>
      </Col>
    </Row>
  );
};
export default SearchPage;
