import React from "react";
import { Row, Col, Typography } from "antd";
import SearchBox from "../SearchBox";
import GlobalExclusionTable from "../GlobalExclusionTable";
import newdata from "../../data/products-visely.json";
import globalExclusionData from "../../data/global-exclusion.json";

const GlobalExclusion = () => {
  const { Title } = Typography;

  return (
    <div
      style={{
        padding: "22px 26px",
        backgroundColor: "#fff",
        border: "1px solid #f0f0f0",
        borderTop: "none",
      }}
    >
      <Row gutter={26}>
        <Col className="gutter-row" span={12}>
          <SearchBox
            hintLabel="HIDE PRODUCTS"
            hintTitle="— Choose products that should be excluded from search and filter
          results. Put them back easily."
            label="Search term"
            tooltipTitle="Example"
            placeholder="Dress"
          />
        </Col>
      </Row>
      <Row gutter={26}>
        <Col className="gutter-row" span={12}>
          <Title level={5} style={{ margin: 0 }}>
            ALL PRODUCTS
          </Title>
          <GlobalExclusionTable newdata={newdata} removable={true} />
        </Col>
        <Col className="gutter-row" span={12}>
          <Title level={5} style={{ margin: 0 }}>
            ALL PRODUCTS
          </Title>
          <GlobalExclusionTable
            newdata={globalExclusionData}
            removable={false}
          />
        </Col>
      </Row>
    </div>
  );
};
export default GlobalExclusion;
