import React, { useState } from "react";
import { Table, Typography, Button } from "antd";
import { CloseOutlined, CheckOutlined } from "@ant-design/icons";
import clsx from "clsx";
import classes from "./styles.module.scss";

const GlobalExclusionTable = (props) => {
  const { newdata, removable } = props;
  const [state, setState] = useState({
    selectedRowKeys: [],
  });
  const { selectedRowKeys } = state;
  const { Text } = Typography;

  const columns = [
    {
      title: () => (
        <div className={classes.theadCell}>
          <div className={classes.selected}>
            {state.selectedRowKeys.length} selected
          </div>
          {removable && (
            <Button
              icon={<CloseOutlined className={classes.buttonIcon} />}
              className={clsx(classes.theadButton, classes.theadButtonDanger)}
            />
          )}

          <Button
            disabled={removable && true}
            icon={<CheckOutlined className={classes.buttonIcon} />}
            className={classes.theadButton}
          />
        </div>
      ),
      dataIndex: "name",
      key: "name",
      render: (text, row, index) => (
        <div className={classes.product}>
          <div className={classes.col}>
            <div className={classes.imgBox}>
              <img src={row.media} alt="" className={classes.img} />
            </div>
            <Text className={classes.name}>{row.name.en}</Text>
          </div>
        </div>
      ),
    },
    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      align: "center",
      render: (text, row, index) =>
        !row.status && <CloseOutlined className={classes.icon} />,
    },
  ];

  const data = [];

  newdata?.products.forEach((product) => {
    data.push({
      key: product.id,
      name: product.name,
      media: product.media[0].url,
      status: product.inStock,
    });
  });

  const onSelectChange = (selectedRowKeys) => {
    setState({ selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectChange,
  };

  return (
    <Table
      bordered={true}
      rowSelection={rowSelection}
      columns={columns}
      dataSource={data}
      pagination={{
        position: ["bottomLeft"],
        defaultPageSize: 10,
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "30"],
        hideOnSinglePage: true,
      }}
    />
  );
};
export default GlobalExclusionTable;
