import React, { useState, useEffect, useCallback } from "react";
import { Row, Col, Tabs, Typography } from "antd";
import SearchBox from "../SearchBox";
import ProductsTable from "../ProductsTable";
import newdata from "../../data/products-visely.json";
import previouslyExcludedData from "../../data/previously-excluded.json";

const CollectionPage = () => {
  const [openDatePicker, setOpenDatePicker] = useState(false);
  const { TabPane } = Tabs;
  const { Title } = Typography;

  const handleScroll = useCallback(() => {
    setOpenDatePicker(false);
  }, []);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
  }, [handleScroll]);

  const handleDatePicker = () => {
    setOpenDatePicker((prevState) => !prevState);
  };

  return (
    <Row>
      <Col span={24}>
        <SearchBox
          hintLabel="COLLECTION"
          hintTitle="— Pin or exclude products for a specific collection. Overwrite the collection order."
          label="Search term"
          tooltipTitle="Example"
          placeholder="Dress"
          marginBottom={6}
        />
        <Tabs defaultActiveKey="0" tabPosition={"top"}>
          {newdata.collectionCategories.map((item, i) => (
            <TabPane tab={`${item.name}`} key={i} disabled={i === 28}>
              <ProductsTable
                newdata={newdata}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
              <Title level={5} style={{ margin: "21px 0 0" }}>
                Previously Excluded
              </Title>

              <ProductsTable
                type={"previouslyExcluded"}
                newdata={previouslyExcludedData}
                openDatePicker={openDatePicker}
                handleDatePicker={handleDatePicker}
              />
            </TabPane>
          ))}
        </Tabs>
      </Col>
    </Row>
  );
};
export default CollectionPage;
