import React from "react";
import { Layout, Tabs } from "antd";
import "antd/dist/antd.css";
import Header from "./components/Header";
import Breadcrumb from "./components/Breadcrumb";
import Nabar from "./components/Navbar";
import PinsAndExcludes from "./components/PinsAndExcludes";
import GlobalExclusion from "./components/GlobalExclusion";
import "./App.scss";
import Logo from "./images/icons/Logo";

function App() {
  const { Sider, Content } = Layout;
  const { TabPane } = Tabs;

  return (
    <Layout style={{ minHeight: "100vh" }}>
      <Sider className="sider">
        <div className="navbarLogoBox">
          <Logo />
        </div>
        <Nabar />
      </Sider>
      <Layout>
        <Header />
        <Breadcrumb />
        <Content style={{ margin: "0 24px 25px" }}>
          <Tabs type="card">
            <TabPane tab="Pins & Excludes" key="1">
              <PinsAndExcludes />
            </TabPane>
            <TabPane tab="Global Exclusion" key="2">
              <GlobalExclusion />
            </TabPane>
          </Tabs>
        </Content>
      </Layout>
    </Layout>
  );
}

export default App;
