import React, { useState } from "react";
import { Row, Col, Form, Divider, Radio, Space, Typography } from "antd";
import SearchPage from "../SearchPage";
import CollectionPage from "../CollectionPage";

const PinsAndExcludes = () => {
  const [page, setPage] = useState("search");
  const { Title, Text } = Typography;

  const handlePageChange = (e) => {
    setPage(e.target.value);
  };

  return (
    <div
      style={{
        padding: "22px 26px",
        backgroundColor: "#fff",
        border: "1px solid #f0f0f0",
        borderTop: "none",
      }}
    >
      <Row style={{ margin: "0 0 16px 0" }}>
        <Col span="24">
          <Space>
            <Title level={5} style={{ margin: "0" }}>
              PIN PAGE
            </Title>
            <Text type="secondary">
              — Select the page for which to set up pins
            </Text>
          </Space>
        </Col>
      </Row>
      <Row>
        <Col span="24">
          <Form layout={"horizontal"}>
            <Form.Item
              label={"Select page"}
              tooltip={{
                title: "Example",
              }}
              style={{ margin: "0 0 6px 0" }}
            >
              <Radio.Group
                options={[
                  { label: "Search", value: "search" },
                  { label: "Collection", value: "collection" },
                ]}
                onChange={handlePageChange}
                value={page}
                optionType="button"
                buttonStyle="solid"
                style={{ margin: "0 0 0 12px" }}
              />
            </Form.Item>
          </Form>
          <Divider />
        </Col>
      </Row>
      {page === "search" ? <SearchPage /> : <CollectionPage />}
    </div>
  );
};
export default PinsAndExcludes;
